<template>
 <!-- @cell-mouse-enter="cellMouseEnter" 
        @cell-mouse-leave="cellMouseLeave"
        :cell-style="{paddingTop: '4px', paddingBottom: '4px'}" -->
    <el-table
        highlight-current-row
        :data="tableData"
        row-key="rowkeyid"
        :height="height"
        style='width: 100%;'
        :border="false"
       ref="table"
        
    > 
        <el-table-column
                    width="60px"
                    label=""
                    type="index"
            
                    >
        </el-table-column>

        <el-table-column
            label=""
            width="300px">
            <template slot-scope="scope">
                <el-input clearable v-model="scope.row.describe" placeholder=""></el-input>
            </template>
        </el-table-column>

        <el-table-column label="" width="150px">
            <template slot-scope="scope">
                <el-select v-model="scope.row.action" filterable placeholder="" clearable
                
                >
                    <el-option
                    v-for="item in actionOptions"
                    :key="item.value"                    
                    :value="item.value">
                    </el-option>
                </el-select>
            </template>
        </el-table-column>
        <el-table-column label="" width="150px">
            <template slot-scope="scope">
                <el-select v-model="scope.row.locationType" 
                filterable 
                placeholder="" 
                clearable
                :disabled="scope.row.action===''"
                >
                    <el-option
                    v-for="item in locationTypeOptions"
                    :key="item.value"                    
                    :value="item.value">
                    </el-option>
                </el-select>
            </template>
        </el-table-column>
   
        <el-table-column
            label=""
            width="400px">
            <template slot-scope="scope">
                <el-input clearable 
                v-model="scope.row.locationValue" 
                placeholder=""
               :disabled="scope.row.locationType===''"
                ></el-input>
            </template>
        </el-table-column>
        <el-table-column label="" width="150px">
            <template slot-scope="scope">
                <el-select 
                v-model="scope.row.CheckValueOurce" 
                filterable 
                placeholder="" 
                clearable
                :disabled="scope.row.action!==''"
                >
                    <el-option
                    v-for="item in CheckValueOurceOptions"
                    :key="item.value"                    
                    :value="item.value"
                    
                    >
                    </el-option>
                </el-select>
            </template>
        </el-table-column>
  
        <el-table-column
            label=""
            width="200">
            <template slot-scope="scope">
                <el-input clearable v-model="scope.row.AttributeName" placeholder=""
                :disabled="scope.row.CheckValueOurce!==''"
                
                ></el-input>

            </template>
        </el-table-column>
        <el-table-column label="" width="150px">
            <template slot-scope="scope">
                <el-select v-model="scope.row.CheckType" 
                filterable 
                placeholder="" 
                clearable
                :disabled="scope.row.action!==''"
                >
                    <el-option
                    v-for="item in CheckTypeOptions"
                    :key="item.value"                    
                    :value="item.value">
                    </el-option>
                </el-select>
            </template>
        </el-table-column>
  
        <el-table-column
            label=""
            width="300px">
            <template slot-scope="scope">
                <el-input clearable v-model="scope.row.DataItem" placeholder=""></el-input>
            </template>
        </el-table-column>
        <el-table-column
        fixed="right"
        label=""
        width="150px"
        >
            <template slot-scope="scope">
                <el-row >
                    <el-button
                        icon="el-icon-circle-plus-outline"
                        size="mini"
                        type="info"
                        @click="handleEdit(scope.$index, scope.row)">
                    </el-button>

                    <el-button
                        icon="el-icon-delete"
                        size="mini"
                        type="danger"
                        v-show="scope.$index !== 0"
                        @click="handleDelete(scope.$index, scope.row)">
                    </el-button>
                </el-row>

            </template>
        </el-table-column>
        
    </el-table>

</template>

<script>
  import Sortable from 'sortablejs';
    export default {
        props: {
            save: Boolean,
            testcase: {
                require: false
            }
        },

        methods: {
            S4() {
                return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
            },
           guid() {
                return (this.S4()+this.S4()+"-"+this.S4()+"-"+this.S4()+"-"+this.S4()+"-"+this.S4()+this.S4()+this.S4());
                              },
                        // 
            rowDrop () {
               // 
                const tbody = this.$refs.table.$el.querySelector('.el-table__body-wrapper tbody');
                const _this = this;
                Sortable.create(tbody, {
           //  
                  draggable: ".el-table__row",
                   onEnd ({ newIndex, oldIndex }) {
                        const currRow = _this.tableData.splice(oldIndex, 1)[0];
                        _this.tableData.splice(newIndex, 0, currRow);
                    }
                });
            },
            // 
            columnDrop () {
                const wrapperTr = document.querySelector('.el-table__header-wrapper tr');
                this.sortable = Sortable.create(wrapperTr, {
                    animation: 180,
                    delay: 0,
                    onEnd: evt => {
                        const oldItem = this.dropCol[evt.oldIndex];
                        this.dropCol.splice(evt.oldIndex, 1);
                        this.dropCol.splice(evt.newIndex, 0, oldItem);
                    }
                });
            },
          
            // cellMouseEnter(row) {
            //     this.currentRow = row;
            // },

            // cellMouseLeave(row) {
            //     this.currentRow = '';
            // },
 
            handleEdit(index, row) {
     
                
                this.tableData.push({
                        rowkeyid: this.guid(),
                        describe: '',
                        action: '',
                        locationType: '',
                        locationValue:'',
                        CheckValueOurce:'',
                        AttributeName:'',
                        CheckType:'',
                        DataItem:''
                });
           
            },

            handleDelete(index, row) {
         
                this.tableData.splice(index, 1);
            },

            // 
            parsetestcase() {
                console.log(this.tableData)
                let testcase = this.tableData    
               
                return testcase;
            }
        },
        watch: {
            save: function () {
                
                this.$emit('testcase', this.parsetestcase(), this.tableData);
            },

            testcase: function () {
             
                if (this.testcase.length !== 0) {
                    this.tableData = this.testcase;
                    
                }
            }
        },
        mounted () {
           
            this.rowDrop();
            this.columnDrop();
        },
        computed:{
           
            height() {
                return window.screen.height -440
            }
        },
        data() {
            return {           
                disabled:false,
                actionOptions: [
                    {value:''},
                {
                    value: ''
                },{
                    value: ''
                }, {
                    value: ''
                }, {
                    value: ''
                }, {
                    value: ''
                }, {
                    value: ''
                }, 
                {
                    value: 'iframe'
                }, 
                  {
                    value: 'iframe'
                }, 
                        ],
                locationTypeOptions: [
                    {
                    value: 'XPATH'
                },{
                    value: 'ID'
                }, {
                    value: 'NAME'
                }, {
                    value: 'CLASS_NAME'
                }, {
                    value: 'CSS'
                }, {
                    value: 'LINK_TEXT'
                }, 
                {
                    value: 'PARTIAL_LINK_TEXT'
                }, 
                  {
                    value: 'TAG_NAME'
                }, 
                        ],
                CheckValueOurceOptions: [
                    {
                    value: ''
                },{
                    value: ''
                }, 
                        ],
                CheckTypeOptions: [
                    {
                    value: ''
                },{
                    value: ''
                }, 
                {
                    value: ''
                },{
                    value: ''
                }, 
                {
                    value: ''
                },{
                    value: ''
                }, 
                {
                    value: ''
                },{
                    value: ''
                }, 
                {
                    value: ''
                }, 
                {
                    value: ''
                }, 
                {
                    value: ''
                }, 
                {
                    value: ''
                }, 
                        ],

                currentRow: '',
                tableData: [{     
                        
                        rowkeyid: this.guid(),                  
                        describe: '',
                        action: '',
                        locationType: '',
                        locationValue:'',
                        CheckValueOurce:'',
                        AttributeName:'',
                        CheckType:'',
                        DataItem:''
                    },]
            }
        },
        name: "Testcases"
    }
</script>

<style scoped>
</style>



